import {MYCASHLIST} from '../../../utils/api.js'
export default {
	data() {
		return {
			inviteList: ''
		}
	},
	onLoad() {
		const params = {
			page: 1
		}
		this.$request.url_request(MYCASHLIST,params,'GET',res =>{
			this.inviteList = JSON.parse(res.data).data
			console.log(this.inviteList)
		},err =>{})
	},
	methods: {
		goback(){
			uni.navigateBack({
				delta:1
			})
		}
	}
}
